import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let store = new Vuex.Store({
    state:{
        carPanelData: [],
        maxOff: false,
        carShow: false,
        carTime: null,
        receiveInfo:[{
            "name": "王某某",
            "phone": "13811111111",
            "areaCode": "010",
            "landLine": "64627856",
            "provinceId": 110000,
            "province": "北京市",
            "cityId": 110100,
            "city": "市辖区",
            "countyId": 110106,
            "county": "海淀区",
            "add": "上地十街辉煌国际西6号楼319室",
            "default": true
          },{
            "name": "李某某",
            "phone": "13811111111",
            "areaCode": "010",
            "landLine": "64627856",
            "provinceId": 110000,
            "province": "北京市",
            "cityId": 110100,
            "city": "市辖区",
            "countyId": 110106,
            "county": "海淀区",
            "add": "上地十街辉煌国际东6号楼350室",
            "default": false
          }],
        orderData:[]
    },
    getters:{
        totalCount(state){
            let count = 0;
            state.carPanelData.forEach((goods)=>{
                count += goods.count
            })
            return count;
        },
        totalPrice(state){
            let price = 0;
            state.carPanelData.forEach((goods)=>{
                price += goods.price * goods.count
            })
            return price;
        },
        allChecked(state){
            let allChecked = true
            state.carPanelData.forEach((goods)=>{
                if(!goods.checked){
                    allChecked = false
                    return
                }
            })
            return allChecked
        },
        checkedCount(state){
            let count = 0
            state.carPanelData.forEach((goods)=>{
                if(goods.checked){
                    count += goods.count
                }
            })
            return count
        },
        checkedPrice(state){
            let price = 0
            state.carPanelData.forEach((goods)=>{
                if(goods.checked){
                    price += goods.count * goods.price
                }
            })
            return price
        },
        checkedGoods(state){
            let checkedGoods = []
            state.carPanelData.forEach((goods)=>{
                if(goods.checked){
                    checkedGoods.push(goods)
                }
            })
            return checkedGoods
        }

    },
    mutations:{
        addCarPanelData(state,data){
            let boff = true;
            let showTime = null;
            state.carPanelData.forEach((goods)=>{
                if(goods.sku_id === data.info.sku_id){
                    goods.count+=data.count;
                    boff = false
                    if(goods.count >= goods.limit_num+1){
                        goods.count = goods.limit_num
                        state.maxOff = true
                        clearTimeout(showTime)
                        return
                    }
                    clearTimeout(showTime)
                    state.carShow = true
                }
            })
            if(boff){
                let goodsData = data.info
                Vue.set(goodsData,'count',data.count)
                Vue.set(goodsData,'checked',true)
                state.carPanelData.push(goodsData)
                state.carShow = true
            }
            showTime = setTimeout(_ =>{
                state.carShow = false
            },500)
        },
        deleteCarPanelData(state,id){
            state.carPanelData.forEach((goods,index)=>{
                if(goods.sku_id === id){
                    state.carPanelData.splice(index,1)
                    return
                }
            })
        },
        closePrompt(state){
            state.maxOff = false
        },
        carShow(state){
            clearTimeout(state.carTime)
            state.carShow = true
        },
        carHide (state){
            state.carTime = setTimeout( _ => {
                state.carShow = false
            },750 )
        },
        plusCarPanelData(state, id){
            state.carPanelData.forEach((goods,index)=>{
                if(goods.sku_id === id){
                   if(goods.count >= goods.limit_num) return
                   goods.count ++
                   return
                }
            })
        },
        subCarPanelData(state, id){
            state.carPanelData.forEach((goods,index)=>{
                if(goods.sku_id === id){
                    if(goods.count <= 1) return
                    goods.count --
                    return
                 }
            })
        },
        checkedGoods(state, id){
            state.carPanelData.forEach((goods,index)=>{
                if(goods.sku_id === id){
                    goods.checked = !goods.checked
                    return
                 }
            })
        },
        allCheckedChange(state,checked){
            state.carPanelData.forEach((goods,index)=>{
                goods.checked = !checked
            })
        },
        deleteChecked(state){
            let i = state.carPanelData.length
            while(i--){
                if(state.carPanelData[i].checked){
                    state.carPanelData.splice(i,1)
                }
            }
        },
        submitReceive(state,data){
            if(data.default){
                state.receiveInfo.forEach((receive)=>{
                    receive.default = false
                })
            }
            state.receiveInfo.unshift(data)
        },
        submitOrder(state,data){
            state.orderData.unshift(data)
            let i = state.carPanelData.length
            while (i--){
                if(state.carPanelData[i].checked){
                    state.carPanelData.splice(i,1)
                }
            }
            console.log(state.orderData)
        },
        payNow(state, id){
            state.orderData.forEach((order)=>{
                if(order.orderId === id){
                    order.isPay = true
                    return
                }
            })
        },
        setDefault(state, index){
            state.receiveInfo[index].default = true
        },
        deleteReceive(state, index){
            state.receiveInfo.splice(index,1)
        }
    }
})

export default store


